    padding: 0 3.5em;
  }
}
.team-block .text .title {
  font-size: 27px;
  text-transform: uppercase;
  margin-bottom: 5px;
}
@media (min-width: 2px) and (max-width: 1169px) {
  .team-block .text .title {
    display: none!important;
  }
}
.team-block .text .undertitle {
  font-size: 14px;
  color: #848484;
  font-weight: 100;
  margin-bottom: 20px;
}
@media (min-width: 2px) and (max-width: 1169px) {
  .team-block .text .undertitle {
    display: none!important;
  }
}
.team-block .text .desc {
  font-size: 14px;
  line-height: 23px;
  font-weight: 100;
}
.team-block:last-of-type {
  margin-bottom: 100px;
}
.team-block:last-of-type .img {
  order: 0;
}
@media (min-width: 2px) and (max-width: 1169px) {
  .team-block:last-of-type .img img {
    order: 0;
  }
  .team-block:last-of-type .img-title {
    text-align: left;
    padding-left: 20px;
    padding-right: 0;
  }
}
