.zoo-item {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
  transition: all 150ms linear;
  display: flex;
  align-items: center;
  justify-content: center
}

.zoo-item .zoo-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transition: transform 0.5s ease-out
}
